import axios from 'axios';
import { BASE_PATH } from '@/consts';

// Chapter model reflecting only necessary attributes
export interface Chapter {
  id: string;
  type: string;
  attributes: {
    title: string;
    slug: string;
    order: number;
    summary?: string;  // Summary is optional
  };
  relationships: {
    book: {
      data: {
        id: string;
        type: string;
      };
    };
  };
}

// Book details model including an optional list of chapters
export interface BookDetails {
  id: string;
  type: string;
  attributes: {
    title: string;
    author: string;
    cover: string;
    pages: number;
    release_date: string;
    summary: string;
  };
  chapters?: Chapter[]; // Chapters are optional here, they will be fetched separately
}

export interface ApiResponse<T> {
  data: T;
}

// Fetch book details along with chapters
export const fetchBookDetails = async (id: string): Promise<BookDetails | null> => {
  try {
    // Fetch the book details
    const bookResponse = await axios.get<ApiResponse<BookDetails>>(`${BASE_PATH}/books/${id}`);
    const bookDetails = bookResponse.data.data;

    // Fetch the chapters for the book
    const chaptersResponse = await axios.get<ApiResponse<Chapter[]>>(`${BASE_PATH}/books/${id}/chapters`);
    const chapters = chaptersResponse.data.data;

    // Combine the book details with chapters
    return { ...bookDetails, chapters };
  } catch (error) {
    console.error('Error fetching book details:', error);
    return null;
  }
};

// Fetch a specific chapter for a given book
export const fetchChapterContent = async (bookId: string, chapterId: string): Promise<Chapter | null> => {
  try {
    // Fetch the specific chapter for the book
    const chapterResponse = await axios.get<ApiResponse<Chapter>>(`${BASE_PATH}/books/${bookId}/chapters/${chapterId}`);
    return chapterResponse.data.data;
  } catch (error) {
    console.error('Error fetching chapter content:', error);
    return null;
  }
};
